import { ICacheObject, ICacheProvider, Key } from "../types";

/**
 * 默认缓存
 * @description
 * 默认缓存使用Map作为缓存容器，存储速度快。但不支持自定义的key对比，对于简单类型的key使用值对比，对于引用类型的key，对比的是引用的地址。
 */
export class DefaultCacheProvider<T> implements ICacheProvider<T>{
  private buffer: Map<any, ICacheObject<T>>;

  constructor() {
    this.buffer = new Map<any, ICacheObject<T>>();
  }
  public has(key: Key): boolean {
    return this.buffer.has(key);
  }
  public length(): number {
    return this.buffer.size;
  }
  public set(value: ICacheObject<T>) {
    this.buffer.set(value.key, value);
  }
  public get(key: Key): ICacheObject<T> {
    return this.buffer.get(key);
  }
  public delete(key: Key) {
    this.buffer.delete(key);
  }
  public clear() {
    this.buffer.clear();
  }
  public keys(): IterableIterator<Key> {
    return this.buffer.keys();
  }
  public values(): IterableIterator<ICacheObject<T>> {
    return this.buffer.values();
  }
}