import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable } from "rxjs";
import { Listeners } from "../listener/index";
import { Expression } from "./types";
/**
 * 事件监听器主要有以下几种类型的事件：
 * 1、值变化
 * 2、行切换
 *    行切换使用场景为需要为数据计算依赖当前行时
 * 3、数据加载
 */
@Injectable()
export class ExpressionEventEmitter {
  private onEvent: BehaviorSubject<Array<Expression.EventArgs>>;
  private events: Array<Expression.EventArgs>;
  constructor(private listeners: Listeners) {
    this.events = new Array<Expression.EventArgs>();
    this.listeners.onEvent.subscribe((eventArgs: Expression.EventArgs) => {
      if (this.onEvent && this.onEvent.observers.length > 0) {
        let events = [];
        if (this.events.length > 0) {
          events = [...this.events];
        }
        events.push(eventArgs);
        this.onEvent.next(events);
        this.events = [];
      } else {
        this.events.push(eventArgs);
      }
    });
  }
  public attach(): Observable<Array<Expression.EventArgs>> {
    if (!this.onEvent) {
      this.onEvent = new BehaviorSubject<Array<Expression.EventArgs>>(this.events);
    }
    return this.onEvent.asObservable();
  }
}