import { EnumData, FormVariable } from './dom-entity';

export interface SchemaType {
    name: string;
    code: string;
    label: string;
    fields?: SchemaEntityField[];
    entities?: SchemaEntity[];
    primary?: string;
}

/**
 * schema
 */
export class Schema {
    public dataSource?: string;
    public sourceUri: string;
    public id: string;
    public code: string;
    public name: string;
    public entities: SchemaEntity[];
    public variables: SchemaEntityField[];
    public eapiId: string;
    public extendProperties: { enableStdTimeFormat: boolean };
}

/**
 * 实体
 */
export class SchemaEntity {
    public id: string;
    public code: string;
    public name: string;
    public label: string;
    public type: SchemaEntityType;
}

/**
 * 实体类型对象
 */
export class SchemaEntityType implements SchemaType {
    public code: string;
    public label: string;
    public name: string;
    public primary: string;
    public fields: SchemaEntityField[];
    public entities?: SchemaEntity[];
    public displayName?: string;
}

/**
 * 字段
 */
export class SchemaEntityField {
    public $type: SchemaEntityField$Type;
    public id: string;
    public originalId: string;
    public code: string;
    public label: string;
    public bindingField: string;
    public name: string;
    public defaultValue: any;
    public require: boolean;
    public readonly: boolean;
    public type: SchemaEntityFieldType;
    public editor?: SchemaEntityFieldEditor;
    public path?: string;
    public bindingPath?: string;
    public multiLanguage?: boolean;
    public converter?: any;
    // 表达式
    public expression?: any;
}

/**
 * 字段类型对象
 */
export class SchemaEntityFieldType implements SchemaType {
    code: string;
    label: string;
    public $type: SchemaEntityFieldType$Type;
    public name: SchemaEntityFieldTypeName | any;
    public length?: number;
    public precision?: number;
    public valueType?: SchemaEntityFieldType;
    public enumValues?: EnumData[];
    public fields?: SchemaEntityField[];
    public displayName?: string;
    public primary?: string;
    public entities?: SchemaEntity[];
    // 用于区分日期/日期事件和整型/浮点型数据
    public elementType?: any;


    // 扩展属性:  运行时定制用
    public extendProperty?: any;
}

/**
 * 字段编辑器对象
 */
export class SchemaEntityFieldEditor {
    public $type: any;
    [propName: string]: any;
}


/**
 * 字段类型枚举
 */
export enum SchemaEntityField$Type {
    /**
     * 简单类型字段
     */
    SimpleField = 'SimpleField',
    /**
     * 关联/UDT类型字段
     */
    ComplexField = 'ComplexField'
}

/**
 * 字段类型对象中的类型枚举
 */
export enum SchemaEntityFieldType$Type {

    /**
     * 字符串
     */
    StringType = 'StringType',
    /**
     * 备注
     */
    TextType = 'TextType',
    /**
     * 数字（整数、浮点数）
     */
    NumericType = 'NumericType',
    /**
     * 布尔
     */
    BooleanType = 'BooleanType',
    /**
     * 日期
     */
    DateType = 'DateType',

    /**
     * 日期时间
     */
    DateTimeType = 'DateTimeType',

    /**
     * 枚举
     */
    EnumType = 'EnumType',
    /**
     * 实体类
     */
    EntityType = 'EntityType',

    /**
     * 分级码
     */
    HierarchyType = 'HierarchyType',

    /**
     * 对象
     */
    ObjectType = 'ObjectType',

    /**
     * 数字（大数据）
     */
    BigNumericType = 'BigNumericType'
}


/**
 * 字段类型中的名称
 */
export enum SchemaEntityFieldTypeName {
    /**
     * 简单类型字段
     */
    String = 'String',
    /**
     * 日期时间
     */
    DateTime = 'DateTime',
    /**
     * 日期
     */
    Date = 'Date',
    /**
     * 枚举
     */
    Enum = 'Enum',
    /**
     * 布尔
     */
    Boolean = 'Boolean',

    /**
     * 数字
     */
    Number = 'Number',

    /**
     * 备注
     */
    Text = 'Text',

    /**
     * 大数字
     */
    BigNumber = 'BigNumber'
}
