import { Type } from '@angular/core';
import { makePropDecorator, makeDecorator } from '../metadata/index';
import { ValidatorFn, AsyncValidatorFn } from '@angular/forms';
import { Form } from './form';
import { ControlValueConverter } from './control_value_converter';
import { ValidateRule } from '../entity';

/**
 * ----------------------------------------
 * NgValidateForm
 * ----------------------------------------
 */

/**
 * 子表单装饰器名称
 */
export const NG_VALIDATE_FORM = 'NgValidateForm';

/**
 * 表单描述
 */
export interface NgValidateForm {

  /**
   * Form名称，用于显示验证信息
   */
  formGroupName: string;
  /**
   * 是否启用验证
   */
  enableValidate: boolean;
}

/**
 * 表单装饰器
 */
export interface NgValidateFormDecorator {
  (obj?: NgValidateForm): any;
  new(obj?: NgValidateForm): any;
}

/**
 * 表单装饰器工厂
 */
export function NgValidateForm(options: NgValidateForm) {
  const decoratorFactory = makeDecorator(NG_VALIDATE_FORM, (obj: NgValidateForm) => obj);
  return decoratorFactory(options);
}

/**
 * ----------------------------------------
 * NgChildForm
 * ----------------------------------------
 */

/**
 * 子表单装饰器名称
 */
export const NG_CHILD_FORM = 'NgChildForm';

/**
 * 子表单描述
 */
export interface NgChildForm {

  /**
   * 子表单类型
   */
  formType: Type<Form>;
}

/**
 * 子表单装饰器
 */
export interface NgChildFormDecorator {
  (obj?: NgChildForm): any;
  new(obj?: NgChildForm): any;
}

/**
 * 子表单装饰器工厂
 */
export const NgChildForm: NgChildFormDecorator =
  makePropDecorator(NG_CHILD_FORM, (obj: NgChildForm) => obj);


/**
 * ----------------------------------------
 * NgChildFormArray
 * ----------------------------------------
 */

/**
 * 子表单数组装饰器名称
 * @type {string}
 */
export const NG_CHILD_FORM_ARRAY = 'NgChildFormArray';

/**
 * 子表单数组描述
 */
export interface NgChildFormArray {

  /**
   * 子表单数组内子表单的类型
   */
  formType: Type<Form>;

  /**
   * 多行记录
   */
  multi?: boolean;
}

/**
 * 子表单数组装饰器
 */
export interface NgFormArrayDecorator {
  (obj?: NgChildFormArray): any;
  new(obj?: NgChildFormArray): any;
}

/**
 * 子表单数组装饰器工厂
 */
export const NgChildFormArray: NgFormArrayDecorator =
  makePropDecorator(NG_CHILD_FORM_ARRAY, (obj: NgChildFormArray) => obj);


/**
 * ----------------------------------------
 * NgFormControl
 * ----------------------------------------
 */

/**
 * 表单控件装饰器名称
 * @type {string}
 */
export const NG_FORM_CONTROL = 'NgFormControl';

/**
 * 表单控件描述
 */
export interface NgFormControl {
  /**
   * 控件标识
   */
  id?: string;
  /**
   * 控件名称
   */
  name?: string;

  /**
   * 默认控件名称(多语环境翻译无值时使用)
   */
  defaultI18nValue?: string;

  /**
   * 绑定字段路径
   */
  binding?: string;

  /**
   * 值转换器
   */
  valueConverter?: ControlValueConverter;

  /**
   * 是否禁用
   */
  disabled?: boolean;

  /**
   * 控件值更新时机
   */
  updateOn?: 'change' | 'blur' | 'submit';

  /**
   * 同步验证器
   */
  validators?: ValidatorFn | ValidatorFn[] | null;

  /**
   * 异步验证器
   */
  asyncValidatorFn?: AsyncValidatorFn | AsyncValidatorFn | null;

  /**
   * 验证规则
   */
  validRules?: ValidateRule | ValidateRule[] | null;

  /**
   * 值改变前监听器
   */
  valueChanging?: string;
  /**
   * 值改变后监听器
   */
  valueChanged?: string;
  /**
   * 大数
   */
  bigNumber?: boolean;
  [prop: string]: any;
}

/**
 * 表单控件装饰器
 */
export interface NgFormControlDecorator {
  (obj?: NgFormControl): any;
  new(obj?: NgFormControl): any;
}

/**
 * 表单控件装饰器工厂
 */
export const NgFormControl: NgFormControlDecorator =
  makePropDecorator(NG_FORM_CONTROL, (obj: NgFormControl) => obj);
