import { Injectable } from '@angular/core';
import { Compare, FilterCondition, ObjectFilterCondition } from './types';
import { List } from 'immutable';
import { BindingObject } from '../binding-data';

@Injectable()
export abstract class DataFilter {
  constructor() { }
  abstract filter(data: List<BindingObject>, conditions: Array<FilterCondition> | ObjectFilterCondition);
  protected getCurrentLanguage() {
    return window.localStorage.getItem('languageCode') || 'zh-CHS';
  }
  public getCompareResult(fieldDataValue: any, operator: Compare, targetValue: any) {
    const op = parseInt('' + operator, 10);
    switch (op) {
      case Compare.Equal:
        return fieldDataValue == targetValue;
      case Compare.NotEqual:
        return ('' + fieldDataValue).toLowerCase() !== ('' + targetValue).toLowerCase();
      case Compare.Greater:
        return fieldDataValue > targetValue;
      case Compare.GreaterOrEqual:
        return fieldDataValue >= targetValue;
      case Compare.Less:
        return fieldDataValue < targetValue;
      case Compare.LessOrEqual:
        return fieldDataValue <= targetValue;
      case Compare.Like:
        return ('' + fieldDataValue).toLowerCase().indexOf(targetValue.toLowerCase()) > -1;
      case Compare.NotLike:
        return ('' + fieldDataValue).toLowerCase().indexOf(targetValue.toLowerCase()) === -1;
      case Compare.In:
        targetValue = targetValue || [];
        return targetValue.findIndex((n) => n == fieldDataValue) > -1;
      case 1001: // Empty
        return fieldDataValue === '' || fieldDataValue === null;
      case 1002: // NotEmpty
        return fieldDataValue !== '' && fieldDataValue !== null;
      case 1003: // Null
        return fieldDataValue === null;
      case 1004: // NotNull
        return fieldDataValue !== null;
    }
  }
  public getValue(object: any, propertyName: string) {
    return propertyName.split('.').filter(p => p).reduce((result: any, property: string) => {
      if (result) {
        return result[property] === undefined ? null : result[property];
      } else {
        return null;
      }
    }, object);
  }
}
