/**
 * 绑定列表相关定义
 * @author Witt<jiwt@inspur.com>
 */

// tslint:disable: max-line-length

import { List } from 'immutable';
import { Subject } from 'rxjs';

import { Change, ChangeType } from './changes';
import { BindingObject } from './binding_object';
import { BindingProperty } from './binding_property';
import { BindingObjectFactory } from './binding_object_factory';
import { PropertyUtil } from './property_util';
import { BindingData } from './binding_data';
import { TranslateService } from '../i18n/translate_service';
import { IDisposable } from '../core';
import { FilterCondition, ArrayFilterConditionDataFilter, ObjectFilterCondition, ObjectFilterConditionDataFilter } from '../data-filter';
import { takeUntil } from 'rxjs/operators';

/**
 * BindingList是一个BindingObject集合
 */
class BindingList implements IDisposable {
  public __type__ = 'BindingList';
  /**
   * immutable的BindingObject列表
   */
  private innerList: List<BindingObject>;
  private emptyCurrentItem: BindingObject;
  private sortFields: string[] = [];
  private sortDirections: string[] = [];
  /**
   * 界面数据源
   */
  public defaultView: List<BindingObject> = null;
  /**
   * 关联实体的属性集合
   */
  public properties: BindingProperty[];

  /**
   * 主键名
   */
  public primaryKey: string;

  /**
   * 父对象
   */
  public parent: BindingObject;

  /**
   * 变更流
   */
  public changes: Subject<Change>;

  /**
   * 当前行对应的绑定对象的内码
   */
  public currentId: string;
  /**
   * destory signal
   */
  public destroy$: Subject<any>;

  // #region 分页相关

  /**
   * 分页信息
   */
  public _paginationInfo: any = null;

  set paginationInfo(sPaginationInfo: any) {
    this._paginationInfo = sPaginationInfo;
    if (this._paginationInfo === sPaginationInfo) {
      return;
    }
    this.changes.next({
      type: ChangeType.PaginationInfoChange,
      path: [],
      value: this._paginationInfo
    });
  }

  get paginationInfo(): any {
    return this._paginationInfo;
  }
  /**
   * 获取页码
   */
  get pageIndex() {
    if (!!this.paginationInfo && this.paginationInfo.hasOwnProperty("pageIndex")) {
      return this.paginationInfo.pageIndex;
    }
    return 1;
  }
  /**
   * 获取分页大小
   */
  get pageSize() {
    if (!!this.paginationInfo && this.paginationInfo.hasOwnProperty("pageSize")) {
      return this.paginationInfo.pageSize;
    }
    return 0;
  }

  /**
   * 获取数据总项数
   */
  get total() {
    if (!!this.paginationInfo) {
      return this.paginationInfo.total || this.paginationInfo.totalCount;
    }
    return 0;
  }
  /**
   * 获取跳过的数据条数
   */
  get skip() {
    const pageIndex = this.pageIndex;
    const pageSize = this.pageSize;
    return (pageIndex - 1) * pageSize;
  }

  /**
   * 前台设置分页信息
   * @param skip skip
   * @param take take
   */
  public setPaginationInfo(skip: number, take: number) {
    this.paginationInfo = Object.assign({}, this.paginationInfo, {
      pageSize: take,
      pageIndex: skip / take + 1
    });
    /*this.changes.next({
      type: ChangeType.PaginationInfoChange,
      path: [],
      value: this.paginationInfo
    });*/
  }
  //#endregion

  /**
   * 当前行对应的绑定对象
   * 如果currentId为null，则创建一个空结构，防止绑定报错；
   */
  public get currentItem(): BindingObject {
    const currentItem = this.findById(this.currentId);
    if (!currentItem) {
      if (!this.emptyCurrentItem) {
        this.emptyCurrentItem = BindingObjectFactory.create(this.properties);
      }
      return this.emptyCurrentItem;
    }
    return currentItem;
  }

  /**
   * 绑定对象的数量
   */
  public get length(): number {
    return this.innerList.count();
  }

  /**
   * 构造函数
   */
  constructor(properties: BindingProperty[]) {
    this.properties = properties;
    this.primaryKey = PropertyUtil.getPrimaryKey(properties);

    this.changes = new Subject<Change>();
    this.innerList = List<BindingObject>();
    this.currentId = null;
    this.destroy$ = new Subject();
    this.updateDefaultView(this.changes);
  }
  /**
   * 销毁
   * @param options
   */
  dispose(options?: any) {
    this.clear(true);
    if (this.destroy$) {
      this.destroy$.next();
      this.destroy$.complete();
      this.destroy$ = null;
    }
  }

  /**
   * 添加[Symbol.iterator]，使之能通过for of遍历
   */
  [Symbol.iterator]() {
    const self = this;
    let index = -1;
    const size = this.innerList.size;

    return {
      next: function () {
        index++;
        if (index < size) {
          return { done: false, value: self.innerList.get(index) };
        }
        return { done: true, value: undefined };
      }
    };
  }

  /**
   * 批量加载绑定对象，加载之前先清空绑定列表，并重置当前行，加载之后将第一行设置为默认当前行。
   * @param objects 要加载绑定对象数组
   */
  public load(objects: BindingObject[], entityCreate: boolean = false): void {
    // 重置列表
    this.innerList = this.innerList.clear();
    if (objects.length !== 0) {
      // 加载数据
      objects.forEach((object: BindingObject) => {
        this.add(object);
      });

      // 设置默认当前行
      const currentItem = this.findById(this.currentId);
      if (!currentItem) {
        const firstId = objects[0][this.primaryKey];
        this.setCurrentId(firstId, false, false);
      }
    } else {
      this.currentId = null;
    }
    const change: Change = {
      type: ChangeType.Load,
      path: [],
      value: objects
    };
    change.create = entityCreate;
    // 触发事件
    this.changes.next(change);
  }

  /**
   * 批量追加绑定对象，追加之后将最后一个追加的绑定对象设置为当前行。
   * @param objects 要加载绑定对象数组
   */
  public append(objects: BindingObject[], isCloned: boolean = false, options: any = null): void {
    if (objects.length === 0) {
      return;
    }
    // 加载BindingObject
    objects.forEach((object: BindingObject) => {
      this.add(object);
    });
    // 当前行为新追加的最后1行
    const lastId = objects[objects.length - 1][this.primaryKey];
    this.setCurrentId(lastId, true, true);
    // 触发事件
    const change: Change = {
      type: ChangeType.Append,
      path: [],
      value: objects,
      isTreeNodeLoadScene: options && options.isTreeNodeLoadScene
    };
    if (isCloned) {
      change.isCloned = true;
    }
    this.changes.next(change);
  }
  /**
   * 增加数据
   * @param objects 实体
   * @param options 上下文
   * @description 增加实体数据，但不切换当前行
   */
  public addData(objects: BindingObject[], options: any = null): void {
    if (objects.length === 0) {
      return;
    }
    // 加载BindingObject
    objects.forEach((object: BindingObject) => {
      this.add(object);
    });
    // 触发事件
    this.changes.next({
      type: ChangeType.Append,
      path: [],
      value: objects,
      isTreeNodeLoadScene: options && options.isTreeNodeLoadScene
    });
  }
  public insert(object: BindingObject, position?: 1 | -1) {
    const currentIndex = this.innerList.findIndex((obj: BindingObject) => obj.primaryKeyValue === this.currentId);
    // 加载BindingObject
    if (position === 1) {
      this.innerList = this.innerList.insert(currentIndex + 1, object);
    } else if (position === -1) {
      this.innerList = this.innerList.insert(currentIndex, object);
    } else {
      this.innerList = this.innerList.push(object);
    }
    object.parent = this;
    // 监听object变更，并继续向上抛，由于list有当前行的概念，不需要在path中追加路径
    object.changes.subscribe((change: Change) => {
      this.changes.next(change);
    });

    this.setCurrentId(object.primaryKeyValue, true, true);

    // 触发事件
    this.changes.next({
      type: ChangeType.Append,
      path: [],
      value: object,
      detail: {
        type: 'insert',
        position
      }
    });
  }
  /**
   * 添加绑定对象，并建立绑定对象和绑定列表之间的关联。
   * @param object 绑定对象
   */
  public add(object: BindingObject) {
    this.innerList = this.innerList.push(object);
    object.parent = this;

    // 监听object变更，并继续向上抛，由于list有当前行的概念，不需要在path中追加路径
    object.changes.subscribe((change: Change) => {
      this.changes.next(change);
    });
  }

  /**
   * 删除主键值数组对应的绑定对象。
   * @param ids 主键值数组
   */
  public removeByIds(ids: Array<string>): void {
    if (!ids || ids.length === 0) {
      return;
    }

    let nextCurrentId = this.currentId;
    ids.forEach((id: string) => {
      // 如果当前行被删除，计算下一当前行
      if (id === nextCurrentId) {
        nextCurrentId = this.getCurrentIdBeforeDeleting();
      }

      // 删除对象，找不到时跳过
      const index = this.getIndexById(id);
      if (index === -1) {
        return;
      }
      this.innerList = this.innerList.delete(index);
    });

    // 重新设置当前行
    if (this.innerList.count() === 0) {
      this.currentId = null;
    } else {
      this.setCurrentId(nextCurrentId, false, false);
    }

    // 出发行删除事件
    this.changes.next({
      type: ChangeType.Remove,
      path: [],
      value: ids
    });
  }
  /**
   * 删除数据（不切换当前行）
   * @param ids ids
   */
  public removeDataByIds(ids: Array<string>): void {
    if (!ids || ids.length === 0) {
      return;
    }

    ids.forEach((id: string) => {
      // 删除对象，找不到时跳过
      const index = this.getIndexById(id);
      if (index === -1) {
        return;
      }
      this.innerList = this.innerList.delete(index);
    });

    // 出发行删除事件
    this.changes.next({
      type: ChangeType.Remove,
      path: [],
      value: ids
    });
  }
  /**
   * 清空
   */
  public clear(clearOnly: boolean = false) {
    this.innerList.forEach((item: BindingObject) => {
      item['_ENTITY_'] = null;
      item.unsubscribe.next();
      item.unsubscribe.complete();
      item.changes.complete();
      item.viewChanges.complete();
    });
    this.innerList = this.innerList.clear();
    if (!clearOnly) {
      this.currentId = null;
      this.changes.next({
        type: ChangeType.Remove,
        path: [],
        value: []
      });
    }
  }

  /**
   * 如果当前行被删除，删除之前重新计算当前行的位置，并返回下一当前行的主键值。
   * - 如果被删除的行是最后1行，则上移1行；
   * - 其他情况，下移1行。
   */
  public getCurrentIdBeforeDeleting(): string {
    let nextIndex = -1;
    const currentIndex = this.getIndexById(this.currentId);
    if (currentIndex === this.length - 1) {
      nextIndex = currentIndex - 1;
    } else {
      nextIndex = currentIndex + 1;
    }
    return this.getIdByIndex(nextIndex);
  }

  /**
   * 根据主键值获取对应绑定对象
   * @param   id 要查找的主键值
   * @returns 找到时返回对应BindingObject， 找不到时返回null
   */
  public findById(id: string): BindingObject | null {
    let target: BindingObject;
    target = this.innerList.find((item) => {
      return item.getValue(this.primaryKey) === id;
    });
    return target === undefined ? null : target;
  }

  /**
   * 将主键值为id的绑定对象设置为当前行
   * @param  id        要设置的主键值
   * @param  emitEvent 是否发送当前行变更事件
   * @param  force 强制切换
   */
  public setCurrentId(id: string, emitEvent: boolean = true, emitGlobalEvent: boolean = true, force: boolean = false): void {
    if (this.currentId === id && !force) {
      return;
    }

    // 不存在时设置为null
    // const currentObj = this.findById(id);
    // if (!currentObj) {
    //   this.currentId = null;
    // } else {
    //   this.currentId = id;
    // }

    // @todo：找不到时按理应该设置为null，目前是直接返回，框架部分功能依赖该特性。
    const currentObj = this.findById(id);
    if (!currentObj && !force) {
      return;
    }
    this.currentId = id;

    // 发出行切换事件
    if (emitEvent === true) {
      this.changes.next({
        type: ChangeType.SelectionChanged,
        path: [],
        value: this.currentItem,
        force
      });
    }

    // 是否发送全局的行切换事件
    if (emitGlobalEvent === true) {
      this.changes.next({
        type: ChangeType.GlobalSelectionChanged,
        path: [],
        value: this.currentItem,
        force
      });
    }
  }

  /**
   * 根据主键值为id的绑定对象的索引
   * @param id 主键值
   * @returns 找到时返回对应的index，找不到时返回-1
   */
  public getIndexById(id: string): number {
    return this.innerList.findIndex((obj: BindingObject) => {
      return obj[this.primaryKey] === id;
    });
  }

  /**
   * 根据索引位置获取对应绑定对象的主键值
   * @reutrn 找到时返回对应主键值，找不到返回null
   */
  public getIdByIndex(index: number): string {

    if (index < 0 || index > this.length) {
      return null;
    }

    if (this.innerList.has(index) === false) {
      return null;
    }
    const obj = this.innerList.get(index);
    return obj[this.primaryKey];
  }

  /**
   * 转换为BindingObject数组
   */
  public toArray(): BindingObject[] {
    return this.innerList.toArray();
  }
  /**
   * 交互数据位置
   * @param id1 id1
   * @param id2 id2
   */
  public swapById(id1: any, id2: any) {
    const item1 = this.innerList.find((element) => element.primaryKeyValue === id1);
    const item2 = this.innerList.find((element) => element.primaryKeyValue === id2);
    this.innerList = this.innerList.map((bindingObject: BindingObject, index: number) => {
      if (bindingObject.primaryKeyValue === id1) {
        return item2;
      } else if (bindingObject.primaryKeyValue === id2) {
        return item1;
      } else return bindingObject;
    }).toList();
    this.changes.next({
      type: ChangeType.Swap,
      path: [],
      detail: {
        type: 'swap',
        id: [id1, id2]
      }
    });
  }
  /**
   * 转换为JSON对象
   * @param options 配置
   * @returns 普通对象数组
   */
  public toJSON(options?: any): any[] {
    const result = [];
    this.innerList.forEach((obj: BindingObject) => {
      result.push(obj.toJSON(options));
    });
    return result;
  }
  /**
   * 过滤列表数据
   * @param conditions 过滤条件
   */
  public filter(conditions: Array<FilterCondition> | ObjectFilterCondition) {
    if (this.defaultView === null) {
      this.defaultView = this.innerList.toList();
    }
    let list: List<BindingObject>;
    if (Array.isArray(conditions)) {
      const filter = new ArrayFilterConditionDataFilter();
      list = this.defaultView.filter((bindingObject) => {
        return filter.validateRowData(bindingObject, conditions);
      }).toList();
    } else {
      const filter = new ObjectFilterConditionDataFilter();
      list = this.defaultView.filter((bindingObject) => {
        return filter.validateRowData(bindingObject, conditions);
      }).toList();
    }
    // 如果有排序条件，则对过滤后的结果进行排序
    if (this.sortFields && this.sortFields.length > 0) {
      this.innerList = list.sort(this.comparator(this.sortFields, this.sortDirections)).toList();
    } else {
      this.innerList = list;
    }
  }
  /**
   * 获取分页信息
   * @param path 路径
   * @param defaultValue 默认值
   * @throws 路径必须为字符串！
   */
  public getPaginationConfigByPath(path: string, defaultValue?: any) {
    if (!path || path === '/') {
      return this.paginationInfo;
    }
    if (typeof path !== 'string') {
      throw new Error('路径必须为字符串！');
    }
    path = path.substring(1);
    const paths = path.split('/').filter((item) => !!item && item.trim().length > 0).map((item) => item.trim());
    let config = this.paginationInfo;
    paths.forEach((item) => {
      if (config && config.hasOwnProperty(item)) {
        config = config[item];
      } else {
        config = null;
      }
    });
    return !!config ? config : typeof defaultValue !== 'undefined' ? defaultValue : undefined;
  }
  /**
   * 对bindingList就行排序
   * @param fields - 排序字段
   * @param directions - 排序规则字段
   * @param options - 参数
   * @throws argument error
   */
  public sortBy(fields: string | Array<string>, directions: string | Array<string>, options?: { [prop: string]: any }) {
    if (!this.defaultView) {
      this.defaultView = this.innerList.toList();
    }
    // 默认升序
    const arrFields: Array<string> = typeof fields === 'string' ? fields.split(',').filter((p) => p) : fields || [];
    const arrDirections: Array<string> = typeof directions === 'string' ? directions.split(',').filter((p) => p) : directions || [];
    // 排序字段和排序方式应一致
    if (arrFields.length !== arrDirections.length) {
      throw new Error('sortBy:fields and directions not match');
    }
    this.sortFields = arrFields;
    this.sortDirections = arrDirections;
    if (!this.sortFields || this.sortFields.length < 1) {
      if (this.defaultView) {
        this.innerList = this.defaultView.toList();
        // 清空过滤条件后重置原始数据源
        this.defaultView = null;
      }
    } else {
      this.innerList = this.innerList.sort(this.comparator(arrFields, arrDirections)).toList();
    }
  }
  private getValue(target: any, propName: string, isMultiLangProp: boolean = false, currentLanguage: string = 'zh-CHS') {
    if (target instanceof BindingList) {
      target = target.currentItem;
    } else if (target instanceof BindingData) {
      target = target.list.currentItem;
    }
    let result = null;
    if (propName.indexOf('.') === -1) {
      result = target[propName];
    } else {
      const props = propName.split('.');
      for (const prop of props) {
        target = result = this.getValue(target, prop, isMultiLangProp, currentLanguage);
      }
    }
    if (isMultiLangProp && result && result.hasOwnProperty(currentLanguage)) {
      return result[currentLanguage];
    } else {
      return result;
    }
  }
  /**
   * 排序器
   * @param props - 排序字段
   * @param orders - 排序规则
   * @returns
   */
  private comparator(props: Array<string>, orders: Array<string>) {
    return (item1: BindingObject, item2: BindingObject) => {
      return props.reduce((result, prop) => {
        if (result === 0) {
          const bindingProperty = this.properties.find((item) => item.name === prop);
          let isMultiLangProp = false;
          if (bindingProperty) {
            isMultiLangProp = bindingProperty.enableMultiLangInput;
          }
          const currentLanguage = TranslateService.getCurrentLanguage();
          const order = ['asc'].includes(orders[props.indexOf(prop)]) ? 1 : -1;
          let item1Value = this.getValue(item1, prop, isMultiLangProp, currentLanguage);
          let item2Value = this.getValue(item2, prop, isMultiLangProp, currentLanguage);

          if (item1Value === null || item1Value === undefined) { item1Value = ''; }
          if (item2Value === null || item2Value === undefined) { item2Value = ''; }

          if (typeof item1Value === 'string' && typeof item2Value === 'string') {
            const localeCompareResult = item1Value.localeCompare(item2Value);
            result = localeCompareResult * order;
          } else {
            if (item1Value > item2Value) {
              result = order * 1;
            }
            if (item1Value < item2Value) {
              result = order * -1;
            }
          }
        }
        return result;
      }, 0);
    };
  }
  private updateDefaultView(change: Subject<Change>) {
    change.pipe(takeUntil(this.destroy$)).subscribe((change: Change) => {
      if (this.defaultView !== null && ((change.path.length === 0 && [ChangeType.Load, ChangeType.Append, ChangeType.Remove, ChangeType.Swap].includes(change.type)) || (change.type === ChangeType.ValueChanged && change.isBindingListTransmited !== true))) {
        switch (change.type) {
          case ChangeType.Load:
            this.defaultView = this.innerList.toList();
            break;
          case ChangeType.Append: {
            // 插入
            if (change.detail && change.detail.type === 'insert') {
              const position = change.detail.position;
              const object = change.value;
              const currentIndex = this.innerList.findIndex((obj: BindingObject) => obj.primaryKeyValue === this.currentId);
              // 加载BindingObject
              if (position === 1) {
                this.defaultView = this.defaultView.insert(currentIndex + 1, object);
              } else if (position === -1) {
                this.defaultView = this.defaultView.insert(currentIndex, object);
              } else {
                this.defaultView = this.defaultView.push(object);
              }
            } else {
              const objects = change.value as any[];
              this.defaultView = this.defaultView.push(...objects);
            }
            break;
          }
          case ChangeType.Remove:
            const ids = change.value as Array<string>;
            if (ids.length === 0) {
              this.defaultView = this.defaultView.clear();
            } else {
              ids.forEach((id: string) => {
                const index = this.defaultView.findIndex((bindingObject: BindingObject) => bindingObject.primaryKeyValue === id);
                this.defaultView = this.defaultView.delete(index);
              });
            }

            break;
          case ChangeType.ValueChanged:
            const id = change.id;
            const bindingObject = this.defaultView.find((obj: BindingObject) => obj.primaryKeyValue === id);
            const paths = change.path.concat([]);
            const propertyName = paths.pop();
            const target: BindingObject = paths.reduce((object: BindingObject, path: string) => {
              return object[path];
            }, bindingObject) as BindingObject;
            if (target) {
              target.setValue(propertyName, change.value);
            }
            break;
          case ChangeType.Swap:
            const [id1, id2] = change.detail && change.detail.id;
            const item1 = this.defaultView.find((element) => element.primaryKeyValue === id1);
            const item2 = this.defaultView.find((element) => element.primaryKeyValue === id2);
            this.defaultView = this.defaultView.map((bindingObject: BindingObject, index: number) => {
              if (bindingObject.primaryKeyValue === id1) {
                return item2;
              } else if (bindingObject.primaryKeyValue === id2) {
                return item1;
              } else return bindingObject;
            }).toList();
            break;
          // case ChangeType.SortChanged:
          //   const { fields = [], directions = [] } = change.detail || {};
          //   this.defaultView = this.defaultView.sort(this.comparator(fields, directions)).toList();
          //   break;
        }
      }
    });
  }
}

export { BindingList };
