import { Injectable, Injector } from "@angular/core";

/**
 * 表达式计算结果
 */
@Injectable()
export class ExpressionResult {
  constructor(private injector: Injector) {
  }
  /**
   * 更新表达式的值
   * @param expressionId 表达式id
   * @param result 
   */
  public set(expressionId: string, result: any) {
    this[expressionId] = result;
  }
}