import { makePropDecorator } from '../../metadata/index';

export const NG_SUBSCRIPTION = 'NgSubscription';

/**
 * NgSubscription
 * 事件订阅描述
 */
export interface NgSubscription {
    /**
     * 待订阅事件Token, 唯一ID
     */
    token: string;

    /**
     * 待订阅事件名称
     */
    name: string;

    /**
     * 参数映射
     */
    paramMapCollection?: ParamMap[];
}

export class ParamMap {
    from: string;
    frameId: string;
    to: string;
}

/**
 * NgSubscriptionDecorator
 * 事件订阅装饰器接口
 */
export interface NgSubscriptionDecorator {
    (obj?: NgSubscription): any;
    new(obj?: NgSubscription): any;
}

/**
 * NgSubscription
 * 事件订阅属性装饰器工厂
 */
export function NgSubscription(options: NgSubscription) {
    const decoratorFactory = makePropDecorator(NG_SUBSCRIPTION, (obj: NgSubscription) => obj);
    return decoratorFactory(options);
}

export function getNgSubscriptionDecoratorFactory() {
    const decoratorFactory = makePropDecorator(NG_SUBSCRIPTION, (obj: NgSubscription) => obj);
    return decoratorFactory;
}
