import { BindingProperty } from './binding_property';
import { PropertyUtil } from './property_util';
import { BaseBindingObject } from './base_binding_object';
/**
 * BindingObject是Entity在绑定层的一个影射，它将Entity内的数据转换为不可变对象，并用于界面绑定。
 */
class BindingObject extends BaseBindingObject {

  /**
   * 构造函数
   * @param properties 属性集合
   */
  constructor(properties: BindingProperty[]) {
    super();
    this.properties = properties;
    this.primaryKey = PropertyUtil.getPrimaryKey(properties);
  }
  /**
   * 重新加载数据
   * @param data
   */
  /*public load(data: any) {
    throw new Error('Method not implemented.');
  }*/
}

export { BindingObject };
