import { InjectionToken } from '@angular/core';
import { ChangeSetPolicy, IMessageService, INotifyService } from './types';
/**
 * 表单模块路径
 */
export const FORM_PATH_TOKEN = new InjectionToken<string>('@farris/devkit form path token');
/**
 * 后端消息处理器
 */
export const BACK_END_MESSAGE_HANDLER_TOKEN = new InjectionToken<string>('@farris/devkit_back_end_message_handler');
export const MESSAGE_SERVICE_TOKEN = new InjectionToken<IMessageService>('@farris/message_service_token');
export const NOTIFY_SERVICE_TOKEN = new InjectionToken<INotifyService>('@farris/notify_service_token');
/**
 * 变更集提交策略,entire | valid
 * @description entire是指提交所有变更，valid是指仅提交前端校验通过的变更
 */
export const CHANGE_SET_POLICY_TOKEN = new InjectionToken<ChangeSetPolicy>('@farris/changeset_policy_token');
/**
 * 启用服务器端变更检测
 */
export const ENABLE_SERVER_SIDE_CHANGE_DETECTION_TOKEN = new InjectionToken<boolean>('@farris/enable_server_side_change_detection_token');
/**
 * 启用编辑态过滤排序
 */
export const ENABLE_EDIT_STATE_FILTER_SORTING = new InjectionToken<boolean>('@farris/enable_edit_state_filter_sorting_token');
