import { InjectionToken, Optional, Injector, OnDestroy } from '@angular/core';
import { Observable, Subject, BehaviorSubject, EMPTY } from 'rxjs';
import { concatMap, map, takeLast, take, takeUntil, throwIfEmpty } from 'rxjs/operators';

import { FrameContext } from '../frame/index';
import { VariableParseService } from '../variable/index';

import { Command, CommandParams, ParamDescriptions } from './command';
import { CommandContext } from './command_context';
import { TaskFunc, TaskFlow } from './flow/index';
import { TranslateToken } from '../i18n/translate_token';
import { Translate } from '../i18n/translate';
import { IDisposable } from '../core';

/**
 * 命令处理抽象类，所有具体的命令处理类必须继承它，并实现schedule方法。
 */
abstract class CommandHandler implements IDisposable, OnDestroy {

  /**
   * 任务流程图
   */
  private taskFlow: TaskFlow;

  /**
   * 上下文
   */
  protected frameContext: FrameContext;

  /**
   * 变量解析服务
   */
  protected parseService: VariableParseService;

  public commandName: string;
  private destroy$: Subject<any> = new Subject<any>();
  /**
   * 构造函数
   */
  constructor() {
  }
  dispose(options?: any) {
    if (this.destroy$) {
      this.destroy$.next();
      this.destroy$.complete();
    }
    this.frameContext = null;
  }
  ngOnDestroy(): void {
    this.dispose();
  }

  /**
   * 构造执行流程
   */
  abstract schedule();

  /**
   * 初始化
   */
  public init(frameContext: FrameContext, variableParseService: VariableParseService) {
    this.frameContext = frameContext;
    this.parseService = variableParseService;
    this.taskFlow = new TaskFlow();
    this.schedule();

  }

  /**
   * 执行任务
   * @param command 要执行的命令
   * @return 最后一个任务的执行结果
   * @todo：按功能拆分小函数
   */
  public execute(command: Command): Observable<any> {
    const lastTaskResult$ = new Subject();
    const taskFlow = this.taskFlow.clone();

    // setTimeout暂时不能去掉的原因：
    // 1、树表单加载数据，依赖TreeTableBinding里设置的全局变量，需要延后执行加载时机；
    // 2、关闭前命令需要延迟执行。
    setTimeout(() => {
      if (!this.frameContext || this.frameContext.isDisposed) {
        return EMPTY;
      }
      // 1、解析参数
      // 避免解析变量时修改了原始的command
      const { eventParam = null } = {
        ...command
      };
      delete command.eventParam;
      const commandToExecute = JSON.parse(JSON.stringify(command));
      commandToExecute.params = this.paramsTransform(commandToExecute.params);
      commandToExecute.params = this.parseService.parse(commandToExecute.params, this.frameContext, eventParam);
      command.eventParam = eventParam;
      commandToExecute.eventParam = eventParam;
      this.transParamTypes(commandToExecute.params, commandToExecute.paramDescriptions);

      // 2、串联任务流
      const initContext = new CommandContext(commandToExecute, this.frameContext);
      initContext.eventParam = command.eventParam || null;
      const context$ = new BehaviorSubject<CommandContext>(initContext);
      let currentTask = taskFlow.getNext('', initContext);
      const highOrder$ = context$.pipe(
        concatMap((context: CommandContext) => {
          const result$ = currentTask.execute(context);
          return result$.pipe(
            take(1),
            map((result: any) => {

              // 写入执行结果
              context.results[currentTask.name] = result;
              context.latestResult = result;
              currentTask = taskFlow.getNext(currentTask.name, context);

              // 操作控制流
              if (currentTask) {
                context$.next(context);
              } else {
                context$.complete();
              }

              // 将结果流转换为context流
              return context;
            }),
            throwIfEmpty(() => {
              context$.complete();
            })
          );
        })
      );

      // 3、执行合并后的任务流
      highOrder$.pipe(
        takeLast(1)
      ).subscribe({
        next: (context: CommandContext) => {
          this.waitForDestroy(context);
          lastTaskResult$.next(context.latestResult);
        },
        error: (error: any) => {
          this.waitForDestroy(initContext);
          this.displayError(error);
          lastTaskResult$.error(error || '');
        },
        complete: () => {
          this.waitForDestroy(initContext);
          lastTaskResult$.complete();
        },
      });

    }, 0);

    return lastTaskResult$;
  }
  /**
   * 等待销毁
   * @param commandContext 
   */
  private waitForDestroy(commandContext: CommandContext) {
    if (!commandContext) {
      return;
    }
    commandContext.clearResults();
    if (this.frameContext && this.frameContext.appContext && this.frameContext.appContext.destorySignal) {
      this.frameContext.appContext.destorySignal.pipe(takeUntil(this.destroy$)).subscribe(() => {
        if (commandContext) {
          commandContext.dispose();
          commandContext = null;
        }
      });
    }
  }
  /**
   * 显示错误信息
   */
  private displayError(error: any) {
    if (!error) {
      return;
    }
    if (!console || !console.error) {
      return;
    }
    console.error(error);
  }
  /**
   * 参数国际化转换方法
   */
  private paramsTransform(params: object) {
    const exp = /\{\{(\w+)\}\}/g;
    if (!params) {
      return null;
    }
    const translateService = this.frameContext && this.frameContext.injector && this.frameContext.injector.get<Translate>(TranslateToken, null) || null;
    const pArray = Object.keys(params);
    const result = {};
    if (pArray.length === 0) {
      return params;
    }
    pArray.forEach((p) => {
      let ele = params[p];
      if (ele && exp.test(ele) && translateService) {
        ele = ele.replace(exp, ($1, $2) => {
          return translateService.transform($2, null);
        });
      }
      result[p] = ele;

    });

    return result;
  }

  /**
   * 添加任务，只有子类可以添加任务，外部不能访问
   * @param name  任务名称
   * @param func 任务函数
   */
  protected addTask(name: string, func: TaskFunc) {
    this.taskFlow.addNode(name, func);
  }

  /**
   * 添加任务，只有子类可以添加任务，外部不能访问
   * @param name  任务名称
   * @param func 任务函数
   */
  protected addLink(from: string, to: string, condition: string | boolean) {
    this.taskFlow.addLink(from, to, condition);
  }

  /**
   * 插入任务
   * @param  name 要扩展的任务名称
   * @param  func 扩展函数
   */
  public insertTask(target: string, name: string, func: TaskFunc) {
    throw new Error('Not Implemented');
  }

  /**
   * 插入任务
   * @param  name 要扩展的任务名称
   * @param  func 扩展函数
   */
  public afterTask(target: string, name: string, func: TaskFunc) {
    throw new Error('Not Implemented');
  }

  /**
   * 替换任务
   * @param  name 要替换的任务名称
   * @param  func 替换函数
   */
  public replaceTask(name: string, func: TaskFunc) {
    throw new Error('Not Implement');
  }

  /**
   * 调用方法
   */
  public invoke(serviceInstance: any, method: string, args: any[], context: CommandContext) {
    this.setContextToServiceInstance(serviceInstance, context);
    const parsedArgs = this.parseService.parse(args, context, context.eventParam);
    return serviceInstance[method](...parsedArgs);
  }

  /**
   * 为服务设置命令上下文
   * @todo
   * 通过这种方式存在很大问题：
   * 1、会覆盖掉已有的context，给开发人员造成困扰和调试成本；
   * 2、服务中依赖了一个没有声明的对象，不符合面向对象的原则。
   * 建议解决方案：
   * 1、将context修改为某个特殊属性名；
   * 2、先检测服务上有没有一个CommandContext类型的context属性，有的话再赋值，
   *    这就要求需要使用context的服务需要是实现一个IContext接口。
   */
  protected setContextToServiceInstance(serviceInstance: any, context: CommandContext) {

    // 如果服务上已经存在context属性，并且该属性不是CommandContext类型，则不能覆盖
    const serviceContext = serviceInstance.context;
    if (serviceContext && (serviceContext instanceof CommandContext === false)) {
      return;
    }

    serviceInstance.context = context;
  }

  /**
   * 根据参数描述信息转换参数类型
   */
  private transParamTypes(params: CommandParams, paramDescriptions: ParamDescriptions) {
    if (!paramDescriptions) {
      return;
    }
    const keys = Object.keys(params);
    keys.forEach(key => {
      if (!paramDescriptions[key] || !paramDescriptions[key].type) {
        return;
      }

      const parType = paramDescriptions[key].type;
      const value = params[key];
      if (value === undefined || value === null || typeof value === parType) {
        return; // 值不存在或类型匹配，无需处理
      }

      switch (parType) {
        case 'string':
          // 其实转换前的参数都是string，这里不会走到
          params[key] = value + '';
          break;
        case 'int':
        case 'double':
        case 'number':
          // 前端数值类型只有number，这里兼容命令构件上设置为int和double的情况
          const numResult = Number(value);
          if (isNaN(numResult)) {
            throw Error(`类型转换失败，参数${key}值为${value}，无法转换为${parType}类型。`);
          }
          params[key] = numResult;
          break;
        case 'boolean':
          let boolResult: boolean;
          const strValue = (value + '').toLowerCase();
          if (strValue === 'true') {
            boolResult = true;
          } else if (strValue === 'false') {
            boolResult = false;
          } else {
            // throw Error(`类型转换失败，参数${key}值为${value}，无法转换为${parType}类型。`);
            boolResult = false;
          }
          params[key] = boolResult;
          break;
        case 'datetime':
          // todo：日期时间暂不处理
          break;
        case 'object':
          // 表达式解析出来的参数，无需处理，按原类型返回
          // todo: 输入参数是个json串，转成object
          break;
        default:
          break;
      }
    });
  }

}

/**
 * 命令处理器注入Token
 */
const COMMAND_HANDLERS_TOKEN = new InjectionToken<CommandHandler>('@Farris Command Handlers');

export { CommandHandler, COMMAND_HANDLERS_TOKEN };
