import { IDisposable } from '../core';
import { FrameContext } from '../frame/index';
import { Command } from './command';

/**
 * Command上下文
 */
class CommandContext implements IDisposable{

  public typeName = 'CommandContext';

  /**
   * 命令实例
   */
  command: Command;

  /**
   * 组件上下文
   */
  frameContext: FrameContext;

  /**
   * 执行结果
   * 记录每一个task执行的结果
   */
  results: { [taskName: string]: any } = {};

  /**
   * 最新的执行结果
   */
  latestResult: any;

  /**
   * 事件参数
   */
  eventParam: any;

  /**
   * 构造函数
   * @param command 命令
   * @param frameContext 框架上下文
   */
  constructor(command: Command, frameContext: FrameContext) {
    this.command = command;
    this.frameContext = frameContext;
  }
  public dispose() {
    this.eventParam = null;
    this.command = null;
    this.results = null;
    this.latestResult = null;
    this.frameContext = null;
  }
  public clearResults(){
    this.results = null;
  }
}

export { CommandContext };
