import { Injector, Injectable, InjectFlags, Optional, ChangeDetectorRef, OnDestroy, ElementRef, Renderer, Renderer2, RendererFactory2, ComponentRef, OnInit } from '@angular/core';
import { FrameContext } from './frame_context';
import { ViewModel } from '../view-model/index';
import { Subscription, Declaration } from '../event-mechanism/index';
import { EventPipe } from '../event-bus-new/index';
import { ComponentType, IDisposable } from '../core/index';
import { FRAME_COMPONENT_INIT_HANDLER_TOKEN, onFrameComponentInit } from './tokens';
import { Subject } from 'rxjs';

@Injectable()
abstract class FrameComponent implements OnInit, OnDestroy, IDisposable {

  /**
   * 变更检测器
   */
  private cd: ChangeDetectorRef;

  /**
   * 框架ID
   */
  public id: string;

  /**
   * 框架上下文
   */
  public context: FrameContext;

  /**
   * 视图模型
   */
  public viewModel: ViewModel;

  /**
   * 订阅事件
   */
  public subscription: Subscription;

  /**
   * 公开事件
   */
  public declaration: Declaration;

  /**
   * 该组件订阅的EventPipes
   */
  private eventPipes: IDisposable[];

  private initialized = false;
  /**
   * 组件销毁流
   */
  public destorySignal: Subject<any>;
  /**
   * 组件类型
   */
  public componentType: ComponentType;
  /**
   * 框架构造函数
   * @param injector 注入器
   */
  constructor(protected injector: Injector) {
    this.context = this.injector.get<FrameContext>(FrameContext, null);
    if (this.context) {
      this.initialize();
    }
    this.destorySignal = new Subject<any>();
    // this.context.init(this);
    // this.viewModel = this.context.viewModel;
    // this.cd = this.getChangeDetectorRef();

    // // 必须先执行context的初始化，然后再初始化Subscription
    // this.initPublicEvent();

    // this.initSubscription();

    // this.restComponent();
  }
  /**
   * 是否为表格组件
   * @description 返回true/false时可以信任，但如果返回的是undefined则不应信任
   * @warning 该属性依赖了生成代码，如果非标准的生成型工程也会导致判断失败。
   */
  public get isGridComponent() {
    if (this.context && this.context.viewModel) {
      const dataGridColumnsName = this.context.viewModel['dataGridColumnsName'] || null;
      return dataGridColumnsName ? true : false;
    }
    return undefined;
  }
  dispose(options?: any) {
    if (this.eventPipes) {
      this.eventPipes.forEach((eventPipe: IDisposable) => {
        (eventPipe as EventPipe).disposeByCaller(this);
      });
    }
    this.cd = null;
    // this.viewModel = null;
    this.context.dispose();
    if (this.destorySignal) {
      this.destorySignal.next();
      this.destorySignal.complete();
    }
  }

  ngOnInit(): void {
    this.initialize();
  }

  private initialize() {
    if (!this.initialized) {
      this.context.init(this);
      this.viewModel = this.context.viewModel;
      this.cd = this.getChangeDetectorRef();

      // 必须先执行context的初始化，然后再初始化Subscription
      this.initPublicEvent();

      this.initSubscription();

      this.restComponent();

      this.onFrameComponentInit();
      this.initialized = true;
    }
  }
  /**
   * 执行组件初始化
   */
  private onFrameComponentInit() {
    const frameComponentInitHandlers = this.injector.get<onFrameComponentInit[]>(FRAME_COMPONENT_INIT_HANDLER_TOKEN, null);
    if (frameComponentInitHandlers && Array.isArray(frameComponentInitHandlers) && frameComponentInitHandlers.length > 0) {
      frameComponentInitHandlers.forEach((handler: onFrameComponentInit) => {
        handler.onComponentInit(this.context);
      });
    }
  }
  /**
   * 获取变更检测器实例
   * @todo：应该通过注入获取，但注入会引起表单编译。
   */
  private getChangeDetectorRef() {

    // const cd = this.get<ChangeDetectorRef>(ChangeDetectorRef, null, InjectFlags.Optional);
    const cd = this.injector.get(ChangeDetectorRef, null);
    return cd;
  }

  /**
   * 将当前组件脱离变更检测树
   */
  public detach() {
    if (this.isCdValid() === false) {
      return;
    }
    this.cd.detach();
  }

  /**
   * 将当前组件重新加入变更检测树
   */
  public reattach() {
    if (this.isCdValid() === false) {
      return;
    }
    this.cd.reattach();
  }

  /**
   * 对当前组件进行一次变更检查
   */
  public detectChanges() {
    if (this.isCdValid() === false) {
      return;
    }
    this.cd.detectChanges();
  }

  /**
   * 检测ChangeDetection是否有效
   * @todo: Can't be depend on the destroyed property, destroyed.
   */
  private isCdValid() {
    return this.cd && this.cd['destroyed'] === false || false;
  }

  /**
   * 重置组件状态
   * @todo：AppContext是全局的，
   */
  public restComponent() {

    if (this.context !== this.context.root) {
      return;
    }

    // 1、如果AppContext不是root并且父AppContext也不是root不清理;
    // 2、表单Module里注入了FARRIS_DEVKIT_APP_PROVIDERS里面有一个冗余的AppContext注入
    //    导致所有AppContext的根是该冗余的AppContext，所以要检测parent.parent。
    // 只清理根组件的session
    if (this.context.appContext.parent !== null && this.context.appContext.parent.parent !== null) {
      return;
    }

    // Repository被注册到全局了，模块依赖注入中的对象，没有重置时机，临时在根组件中进行注销。
    // @todo：应该清理全部repository，目前缺少全局管理所有Repository的地方。
    this.context.repository.reset();
    // 重置组件绑定数据
    this.context.bindingData.reset();
  }

  public ngOnDestroy() {
    this.dispose();
  }

  /**
   * 初始化事件订阅
   */
  private initSubscription() {
    this.subscription = this.getSubscription();
    if (!this.subscription) {
      return;
    }

    this.eventPipes = this.subscription.init(this);
  }


  /**
   * 获取component对应的订阅
   * @returns 
   */
  public getSubscription() {
    return this.injector.get<Subscription>(Subscription, null);
  }

  private initPublicEvent() {

    this.declaration = this.getDeclaration();
    if (!this.declaration) {
      return;
    }

    this.declaration.init(this);
  }

  /**
   * 获取当前component对应的declaration
   * @returns 
   */
  public getDeclaration() {
    return this.injector.get<Declaration>(Declaration, null);
  }

  /**
   * 事件触发器，触发事件发布
   * @param eventName 待发布事件
   */
  public trigger(eventName: string, params?: any) {
    const subscription = this.context.commandBus.executingCommandCount$.subscribe((executingCommandCount: number) => {
      if (executingCommandCount !== 0) {
        return;
      }
      this.innerTrigger(eventName, params);

      // @todo
      // subscription存在未undefine的情况，待进一步排查。
      if (subscription) {
        subscription.unsubscribe();
      } else {
        setTimeout(() => {
          if (subscription) {
            subscription.unsubscribe();
          }
        }, 0);
      }

    });
  }

  /**
   * 内部触发变更检测
   */
  private innerTrigger(eventName: string, params?: any) {

    // 根据事件名，查找对应的事件处理器
    const eventHandler: any = this.declaration && this.declaration[eventName];
    if (!eventHandler) {
      return;
    }
    // 执行事件
    eventHandler(params);
  }

}

export { FrameComponent };
