import { IDisposable } from '../core';
import { ViewModel } from '../view-model';

/**
 * ViewModel管理类
 */
class ViewModelManager implements IDisposable {
  /**
   * ViewModelMap
   */
  private viewModelMap: Map<string, ViewModel>;

  /**
   * 构造函数
   */
  constructor() {
    this.viewModelMap = new Map<string, ViewModel>();
  }

  /**
   * 获取ViewModelMap
   */
  public getViewModelMap(): Map<string, ViewModel> {
    return this.viewModelMap;
  }

  /**
   * 根据name获取ViewModel
   * @return 找不到时返回undefined
   */
  public getViewModelByName(name: string): ViewModel {
    return this.viewModelMap.get(name);
  }

  /**
   * 初始化全局的viewModel
   */
  public register(name: string, viewModel: ViewModel): void {
    this.viewModelMap.set(name, viewModel);
  }

  /**
   * 是否Repository已经存在
   */
  public exsit(name: string) {
    const viewModel = this.getViewModelByName(name);
    return viewModel ? true : false;
  }
  public dispose() {
    this.viewModelMap.clear();
  }

}

export { ViewModelManager };
