import { ComponentRef } from "@angular/core";
/**
 * 组件实例管理
 */
export class ComponentRefManager {
  private components: Map<string, any[]> = new Map<string, any[]>();
  /**
   * 增加组件实例
   * @param id 
   * @param component 
   */
  public add(id: string, component: any) {
    const components = this.components.get(id);
    if (components) {
      components.push(component);
    } else {
      this.components.set(id, [component]);
    }
  }
  /**
   * 删除组件实例
   * @param id 
   */
  public remove(id: string) {
    const components = this.components.get(id);
    if (components && components.length > 0) {
      components.length = 0;
      this.components.delete(id);
    }
  }
  /**
   * 通过id获取组件实例
   * @param id 
   * @returns 
   */
  public get(id: string) {
    if (id) {
      const components = this.components.get(id);
      if (components && components.length > 0) {
        return components;
      }
    }
    return null;
  }
  /**
   * 是否包含id的组件
   * @param id 
   * @returns 
   */
  public has(id: string) {
    return this.components.has(id);
  }
  /**
   * 清空
   */
  public clear() {
    this.components.clear();
  }
  /**
   * 获取指定类型的组件
   * @param componentType 
   * @returns 
   */
  public getComponentByType(componentType: any): any {
    const containers = Array.from(this.components.values());
    let component = null;
    for (let containerIndex = 0; containers && containerIndex < containers.length; containerIndex++) {
      const components = containers[containerIndex];
      for (let index = 0; components && index < components.length; index++) {
        if (components[index] instanceof componentType) {
          component = components[index];
          break;
        }
      }
    }
    return component;
  }
}