import { makePropDecorator } from '../../metadata/index';
import { EventTypeEnum } from './event-type';

export const NG_DECLARATION = 'NgDeclaration';

/**
 * NgDeclaration
 * 公开事件描述
 */
export interface NgDeclaration {
    /**
     * 待订阅事件Token, 唯一ID
     */
    token: string;

    /**
     * 事件名称
     */
    name: string;

    /**
     * 事件参数
     */
    params: any;

    /**
     * 事件类型
     */
    type ?: EventTypeEnum;
}

// /**
//  * 参数
//  */
// export interface Params {
//     [key: string]: any;
// }

/**
 * NgDeclarationDecorator
 * 公开事件装饰器接口
 */
export interface NgDeclarationDecorator {
    (obj?: NgDeclaration): any;
    new(obj?: NgDeclaration): any;
}

/**
 * NgDeclaration
 * 公开事件属性装饰器工厂
 */
export function NgDeclaration(options: NgDeclaration) {
    const decoratorFactory = makePropDecorator(NG_DECLARATION, (obj: NgDeclaration) => obj);
    return decoratorFactory(options);
}
