import { Type } from '@angular/core';
import { Entity } from './entity';
import { EntityTypeCreator } from './entity_type_creator';

/**
 * 创建实体
 * @param entityType 实体类型
 * @param entityData 实体数据
 */
export function createEntity<T extends Entity>(entityType: Type<any>, entityData: any): T {
  let entity: any;
  if (entityType && entityType.prototype && entityType.prototype.typeName === 'ConcreteEntityPrototype') {
    entity = new entityType(entityData);
  } else {
    entity = EntityTypeCreator.create(entityType, entityData);//new entityType(entityData); 
  }
  entity.constructor = entityType;
  return entity;
}

/**
 * 批量创建实体
 * @param entityType     实体类型
 * @param entityListData 实体数据数组
 */
export function createEntities<T extends Entity>(entityType: Type<any>, entityListData: any): T[] {
  const entities: T[] = [];
  entityListData.forEach((entityData: any) => {
    const entity = createEntity<T>(entityType, entityData);
    entities.push(entity);
  });
  return entities;
}

/**
 * 已弃用：请使用createEntity方法代替。
 */
export function EntityFactory<T extends Entity>(T: new (...args: any[]) => any, data: any): T {
  const entity = new T(data);
  return entity;
}



