const validationMessage = {};

/**
 * 验证类型定义
 */
export class ValidationTypes {
  static CURRENT_LANGUAGE = 'zh-CNS';

  /**
   * 条件验证表达式
   */
  static CUSTOM_VALIDATION = `customValidation`;

  /**
   * 必填
   */
  static REQUIRED = 'required';

  /**
   * 比较值是否相同
   */
  static EQUALS = 'equals';

  /**
   * 比较值是否不相同
   */
  static NOT_EQUALS = 'notEquals';

  /**
   * 数字
   */
  static IS_NUMBER = 'isNumber';

  /**
   * 整数
   */
  static IS_INT = 'isInt';

  /**
   * 浮点数
   */
  static IS_FLOAT = 'isFloat';

  /**
   * 字符串
   */
  static IS_STRING = 'isString';

  /**
   * 布尔值
   */
  static IS_BOOLEAN = 'isBoolean';

  /**
   * 日期
   */
  static IS_DATE = 'isDate';

  /**
   * 日期字符串
   */
  static IS_DATE_STRING = 'isDateString';

  /**
   * 布尔值字符串
   */
  static IS_BOOLEAN_STRING = 'isBooleanString';

  /**
   * 数字字符串
   */
  static IS_NUMBER_STRING = 'isNumberString';

  /**
   * 邮箱地址
   */
  static IS_EMAIL = 'isEmail';

  /**
   * JSON
   */
  static IS_JSON = 'isJSON';

  /**
   * 字符串小写
   */
  static IS_LOWERCASE = 'isLowercase';

  /**
   * 字符串大写
   */
  static IS_UPPERCASE = 'isUppercase';

  /**
   * 数字范围
   */
  static RANGE = 'range';

  /**
   * 最小值
   */
  static MIN = 'min';

  /**
   * 最小值
   */
  static MINVALUE = 'minValue';

  /**
   * 最大值
   */
  static MAX = 'max';

  /**
   * 最大值
   */
  static MAXVALUE = 'maxValue';

  /**
   * 长度
   */
  static LENGTH = 'length';

  /**
   * 字符串最大长度
   */
  static MAX_LENGTH = 'maxLength';

  /**
   * 字符串最小长度
   */
  static MIN_LENGTH = 'minLength';

  /**
   * 最小日期
   */
  static MIN_DATE = 'minDate';

  /**
   * 最大日期
   */
  static MAX_DATE = 'maxDate';

  /**
   * 排除非法字符
   */
  static EXCLUDE = 'exclude';

  /**
   * 正则表达式
   */
  static MATCHES = 'matches';

  static FIELD_CONTAINER = 'fieldContainer';
  /**
   * 判断验证类型是否合法
   * @param type 验证类型
   */
  static isValidType(type: string): boolean {
    return (
      type !== `isValidType` &&
      type !== 'getMessage' &&
      Object.keys(this)
        .map(key => (this as any)[key])
        .indexOf(type) !== -1
    );
  }
  /**
   * 获取指定验证类型的提示信息
   * @param type 验证类型
   * @returns 返回验证信息
   */
  static getMessage(type: string): string {
    const messages = validationMessage[this.CURRENT_LANGUAGE] || validationMessage['zh-CHS'];
    return messages[type] || '';
  }

  static setCurrentLanguage(language: string) {
    this.CURRENT_LANGUAGE = language;
  }
}

